.about__image {
  position: relative;
  background-image: linear-gradient(rgba(41, 41, 41, 0.1), rgba(87, 87, 87, 0.5)), url("/assets//img/about-img.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  width: 80%;
  min-height: 30rem;
  border-radius: 2rem;
  overflow: hidden;
