.testimonials {
  text-align: center;
  background-color: #f3f4f6;
}
.testimonials__containter {
  position: relative;
  width: 100%;
  max-width: 1020px;
  padding: 40px 20px;
  margin: auto;
}
.testimonials__containter .arrow svg {
  display: block;
  position: absolute;
  color: #eee;
  cursor: pointer;
  font-size: 2em;
  transition: all 0.3s ease-in-out;
  padding: 5px;
  z-index: 22222222;
}
.testimonials__containter .arrow svg:before {
  cursor: pointer;
}
.testimonials__containter .arrow svg:hover {
  color: orange;
}
.testimonials__containter .arrow .right {
  right: 1rem;
}
.testimonials__containter .arrow .left {
  left: 1rem;
}
.testimonials__containter .dots {
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: 60px;
  left: 0;
  display: block;
  z-index: 3333;
  height: 12px;
}
.testimonials__containter .dots .dot {
  list-style-type: none;
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid #eee;
  margin: 0 10px;
  cursor: pointer;
  -webkit-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  position: relative;
}
.testimonials__containter .dots .dot:active, .testimonials__containter .dots .dot:hover {
  background: #ea830e;
  border-color: #ea830e;
}

.faq .container {
  text-align: center;
  display: grid;
  gap: 20px;
  position: relative;
}
.faq .container .rotate {
  transform: rotate(180deg);
}
.faq .container .hidden {
  display: none;
}
.faq .container .faq__content {
  padding: 45px 0;
}
.faq .container .question__section {
  display: flex;
  justify-content: space-around;
}
.faq .container .question__section input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.faq .container .question__section-button {
  background: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  border: none;
  font-size: 2.4rem;
  height: 5rem;
  border-bottom: 0.2rem solid #2d314d;
  margin: 2rem 0;
}
@media (max-width: 39.9375em) {
  .faq .container .question__section-button {
    font-size: 1.8rem;
  }
}
.faq .container .question__section-button:hover {
  cursor: pointer;
  font-weight: 700;
  color: #2d314d;
}
.faq .container .question__section-button .icon {
  transition: transform 1s;
  cursor: pointer;
}
.faq .container .question__answer {
  margin: 1rem;
  font-size: 1.8rem;
  text-align: start;
  color: black;
}

.header {
  position: relative;
  z-index: 1;
}
@media (max-width: 63.9375em) {
  .header {
    margin-bottom: 1.6rem;
  }
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(#2d314d, transparent);
}
.header nav {
  position: relative;
  background-color: white;
  padding-top: 1.6rem;
  padding-bottom: 1.6rem;
}
.header__logo {
  display: flex;
  align-items: center;
}
.header__logo img {
  width: 7rem;
  height: 7rem;
}
.header__logo span {
  margin-left: 1.6rem;
  color: #2d314d;
  font-size: 2.4rem;
  font-weight: 700;
}
.header__toggle > span {
  display: block;
  width: 2.6rem;
  height: 0.2rem;
  background-color: #2d314d;
  transition: all 300ms ease-in-out;
  transform-origin: 0.3rem 0.1rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.5rem;
}
.header__menu {
  position: absolute;
  width: calc(100% - 4.8rem);
  left: 50%;
  transform: translateX(-50%);
  background: white;
  margin-top: 2.4rem;
  padding: 2.6rem;
  border-radius: 5px;
}
.header__menu a {
  display: block;
  padding: 1rem;
  color: #2d314d;
  text-align: center;
  font-size: 1.8rem;
  border-bottom: 0.1rem solid rgba(45, 49, 77, 0.2);
}
.header__menu a:last-of-type {
  border-bottom: none;
}
.header__menu .poland-button {
  display: flex;
  justify-content: center;
}
.header__menu .poland-button svg {
  margin-top: -0.3rem;
  margin-right: 0.8rem;
}
.header__links a {
  position: relative;
  font-size: 1.6rem;
  color: #9698a6;
  transition: color 300ms ease-in-out;
}
.header__links a:not(:last-child) {
  margin-right: 2.4rem;
}
.header__links a::before {
  content: "";
  display: block;
  position: absolute;
  height: 0.5rem;
  left: 0;
  right: 0;
  bottom: -1.4rem;
  background: linear-gradient(to right, #31d35c, #2bb7da);
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}
.header__links a:hover {
  color: #2d314d;
}
.header__links a:hover::before {
  opacity: 1;
}
.header svg {
  border: 0.1rem solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  box-shadow: rgba(61, 62, 63, 0.3) 0 0.8rem 2.4rem;
}
.header .pracodawca svg {
  height: 3.6rem;
}
.header .pracodawca a {
  color: #2d314d;
  text-align: center;
}

