.info-button {
  position: relative;
  display: inline-block;
  padding: 1.2rem 3.2rem;
  background-image: linear-gradient(to right, #31d35c, #2bb7da);
  border: 0;
  border-radius: 5rem;
  cursor: pointer;
  color: white;
  font-weight: 400;
  font-size: 1.8rem;
  transition: opacity 300ms ease-in-out;
  overflow: hidden;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}
.info-button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.25);
  opacity: 0;
  transition: opacity 150ms ease-in-out;
}
.info-button:hover::before {
  opacity: 1;
}
.info-button.pracodawca {
  background-image: linear-gradient(45deg, rgba(247, 36, 8, 0.8) 0%, rgba(247, 36, 8, 0.8) 50%, white 51%);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
  font-size: 1.8rem;
  font-weight: 700;
}
.info-button.pracodawca span {
  color: rgba(247, 36, 8, 0.8);
}
@media (max-width: 63.9375em) {
  .info-button.pracodawca {
    display: none;
  }
}