.footer {
    background-color: $darkBlue;
    color: $white;
    padding: 4rem;
    @include breakpoint-down(medium) {
      text-align: center;
    }
  
    .container {
      @include breakpoint-up(large) {
        display: grid;
        grid-template-columns: 2fr 1fr 1fr 2fr;
        grid-template-rows: 1fr auto;
        grid-template-areas:
          'logo links1 links2 cta'
          'social links1 links2 copyright';
        gap: 1.6rem;
        justify-items: start;
      }
    }
  
    a {
      color: $white;
    }
  
    &__logo {
      display: inline-block;
  
  
      @include breakpoint-down(medium) {
        margin-bottom: 3rem;
      }
  
      @include breakpoint-up(large) {
        grid-area: logo;
      }

      img {
        @include breakpoint-up(large) {
          height: 10rem;
        }

      }
    }
  
    &__social {
  
      @include breakpoint-down(medium) {
        margin-bottom: 3rem;
      }
  
      @include breakpoint-up(large) {
        grid-area: social;
        align-self: end;
        margin-left: 25%;
      }
  
      a {
        display: inline-block;
        height: 2rem;
  
        svg path {
          transition: fill 150ms ease-in-out;
        }
  
        &:hover svg path {
          fill: $limeGreen;
        }
      }
  
      a:not(:last-child) {
        margin-right: 1.6rem;
      }

      svg {
        @include breakpoint-up(large) {
          height: 4rem;
          width: 4rem;
        }
      }
    }
  
    &__links {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 1.6rem;
  
      @include breakpoint-up(large) {
        justify-content: space-between;
        align-items: start;
      }
  
      &.col1 {
        @include breakpoint-up(large) {
          grid-area: links1;
        }
      }
  
      &.col2 {
        @include breakpoint-down(medium) {
          margin-bottom: 3rem;
        }
  
        @include breakpoint-up(large) {
          grid-area: links2;
        }
      }
  
      a {
        line-height: 2.25;
        transition: color 150ms ease-in-out;
  
        &:hover {
          color: $limeGreen;
        }
      }
    }
  
    &__cta {
      @include breakpoint-up(large) {
        grid-area: cta;
        text-align: end;
        justify-self: end;
      }
  
      a.button {
        @include breakpoint-down(medium) {
          margin-bottom: 3rem;
        }
      }
    }
  
    &__copyright {
      font-size: 1.3rem;
      color: $grayishBlue;
      
      @include breakpoint-up(large) {
        grid-area: copyright;
        align-self: end;
        text-align: end;
        justify-self: end;
      }
    }
  
    .attribution {
      margin-top: 6.4rem;
      font-size: 1.12rem;
      text-align: center;
    }
  }